    import {Decimal} from 'decimal.js'
    import {mapActions} from 'vuex'

    export default {
        name: 'NumberInput',
        data: function () {
            return {
                numericValue: this.value,
                numericStep: this.step
            }
        },
        props: {
            controls: {
                type: Number
            },
            disabled: Boolean,
            value: {
                default: 0,
                type: String | Number
            },
            min: {
                default: 0,
                type: Number
            },
            max: {
                default: 10,
                type: Number | String
            },
            step: {
                default: '1',
                type: String | Function
            },
            inputClass: {
                default: 'm-numberinput__area-input',
                type: String
            },
            buttonClass: {
                default: 'm-numberinput__control-button',
                type: String
            },
            buttonTextClass: {
                default: 'm-numberinput__control-button m-numberinput__control-button-text',
                type: String
            },
            unit: {
                type: String
            },
            len: {
                default: 1,
                type: Number
            },
            remainderWallet: {
                default: 0,
                type: Number | String
            }
        },
        created() {
//            this.saveVuexPN(+this.numericValue)
        },
        methods: {
            ...mapActions([
                'sellPrice',
                'sellNumber'
            ]),
            saveVuexPN(value) {
                if (this.controls === 0) {
                    // 保存价格
                    this.sellPrice(value)
                } else if (this.controls === 1) {
                    // 保存数量
                    this.sellNumber(value)
                }
            },
            formatDeciaml() {
                this.numericValue = new Decimal(this.numericValue)
                this.numericStep = new Decimal(this.numericStep)
            },
//            -------------------加减--------------------
            increaseNumber() {
                this.formatDeciaml()
                this.numericValue = this.numericValue.plus(this.numericStep)
            },
            decreaseNumber() {
                this.formatDeciaml()
                this.numericValue = this.numericValue.minus(this.numericStep)
            },
//            -----------------输入--------------------
//            判断输入时小数点后位数,超出不允许输入
            oninput(e) {
                // eslint-disable-next-line
                const re = new RegExp("^\\d*(\\.?\\d{0," + this.len + "})", "g")
                const number = e.target.value.toString()
                const searchDigital = number.indexOf('.')
                if (searchDigital > 0) {
                    const digit = number.split('.')
                    const digitRight = digit[1]
                    if (digitRight.length >= this.len) {
                        e.target.value = (e.target.value.match(re)[0]) || null
                        this.numericValue = e.target.value
                    } else {
                        this.numericValue = e.target.value
                    }
                } else {
                    this.numericValue = e.target.value
                }
            },
//            ---------------------四分之一------------------------
            quarterNumber() {
                const DTotalNum = new Decimal(this.remainderWallet).div(4).toFixed(this.len)
                this.numericValue = DTotalNum
            },
//            --------------------二分之一-------------------------
            halfNumber() {
                const DTotalNum = new Decimal(this.remainderWallet).div(2).toFixed(this.len)
                this.numericValue = DTotalNum
            },
//            ---------------------全部------------------------
            allNumber() {
                const DTotalNum = new Decimal(this.remainderWallet).div(1).toFixed(this.len)
                this.numericValue = DTotalNum
            }
        },
        watch: {
            value: function () {
                this.numericValue = this.value
            },
            step: function () {
                this.numericStep = this.step
            },
            remainderWallet() {
//                console.log(this.remainderWallet)
            },
            numericValue() {
                this.saveVuexPN(+this.numericValue)
            }
        }
    }



