
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

import {mapState} from 'vuex'

export default {
name: 'exchange-drawer',
props: {

exchangeList: {
type: Array
},
transferSelectIndex: {
type: Number,
default: 0
}
},

data() {
return {
demo1: 1,
tickerList: ['FBC'],
nowTicker: 'FBC',
tab_index: 0,
radioRequired: true,
selectIndex: this.transferSelectIndex,
price: 0
}
},
created() {
},
mounted() {
},
updated() {
},
methods: {

upHandleClick() {
this.$emit('upParents', event)
},
emitIndex() {
this.$emit('upSelectIndex', this.selectIndex)
},
onItemClick(index) {
this.selectIndex = index
this.emitIndex()
this.upHandleClick()
}
},
filters: {
toUpper (val) {
return val.toUpperCase()
}
},
watch: {
transferSelectIndex () {
this.selectIndex = this.transferSelectIndex
}
},
computed: {
...mapState({
exchangePrice: state => state.exchange.exchangePrice,
buyPrice: state => state.exchange.buyPrice
})
},
components: {
Tab,
TabItem,
Checker,
CheckerItem
}
}



