import {mapActions} from 'vuex'
import Decimal from 'decimal.js'
export default {
    name: 'handicap',
    data() {
        return {
            localData: {}
        }
    },
    props: [
        'handicapData'
    ],
    mounted() {
        if (this.handicapData) {
            this.localData = this.handicapData
        }
    },
    methods: {
        ...mapActions([
            'exchangePrice',
            'buyPrice',
            'sellPrice'
        ]),
        setPrice(val) {
            this.exchangePrice(val)
            this.buyPrice(val)
            this.sellPrice(val)
        }
    },
    filters: {
        numFixed(val) {
            return new Decimal(val).toFixed(4)
        }
    },
    watch: {
        handicapData() {
            this.localData = this.handicapData
        }
    }
}



