
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import trim from 'vux/src/tools/string/trim'

import {getUserInfo, ajaxClear} from '@/modules/user/api/get_user'
import {showloadings, hideloadings, showToasts} from '@/utils/load.js'

export default {
name: 'User',
data() {
return {
userInfoData: {},
userName: ''
}
},
created() {
this.getUserInfoData()
},
beforeRouteEnter(from, to, next) {
next(vm => {
vm.getUserInfoData()
})
},
methods: {
getUserInfoData() {
showloadings()
getUserInfo().then(res => {
if (res.status === 200) {
if (res.data.code === '200') {
this.userInfoData = res.data.data
hideloadings()
}
}
})
},
calcUserName() {
if (this.userInfoData.email) {
this.userName = this.userInfoData.email
} else if (this.userInfoData.mobile) {
this.userName = this.userInfoData.mobile
} else {
this.userName = this.userInfoData.uuid
}
},
getChanel() {
ajaxClear().then(res => {
if (res.status === 200) {
if (res.data.code) {
showToasts('已重置', 'success')
hideloadings()
}
}
})
},
resetData() {
const _this = this
this.$vux.confirm.show({
title: '操作提示',
content: '确认重置',
onCancel() {
},
onConfirm() {
_this.getChanel()
showloadings()
}
})
}
},
watch: {
userInfoData() {
this.calcUserName()
}
},
components: {
Group,
Cell,
trim
}
}



